.display2 {
  font-family: 'caesardressingcyrillic';
  font-weight: 400;
  font-size: 32px;
  line-height: 120%;
  color: #291C12;
}
.btn-bg {
  font-family: 'caesardressingcyrillic';
  font-size: 20px;
  line-height: 120%;
  color: #FFFFFF;
  border-radius: 5px;
  background-image: url("../img/catalog-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &:hover {
    background-image: url("../img/delivery-btn.webp");
    color: #291C12;
  }
}
.recommendations-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin-top: 50px;
  gap: 20px;
}
.recommendations-card {
  position: relative;
  &__hashtags {
    position: absolute;
    top: 15px;
    left: -5px;
    display: inline-flex;
    flex-direction: column;
  }
  &__hashtag {
    background: #8AC44B;
    box-shadow: 0px 0px 16px rgba(255, 255, 255, 0.34);
    border-radius: 1px;
    display: inline-flex;
    align-items: center;
    padding: 7px 15px;
    width: fit-content;
    &2 {
      background: #AC6BAD;
    }
    &3 {
      background: #F04438;
    }
    &4 {
      background: #455B66;
    }
    &5 {
      background: #2B74B9;
    }
    span {
      font-weight: 600;
      font-size: 14px;
      line-height: 120%;
      color: #FFFFFF;
      margin-left: 5px;
    }
    i {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &:not(:first-child) {
      margin-top: 5px;
    }
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__desc {
    margin-top: 15px;
    &-top {
      display: flex;
      justify-content: space-between;
    }
    &-bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 7px;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    color: #291C12;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
    max-width: 175px;
  }
  &__weight {
    font-size: 16px;
    line-height: 120%;
    color: #5B5B5B;
    margin-left: 10px;
  }
  &__price {
    display: flex;
    font-weight: 600;
    font-size: 20px;
    line-height: 120%;
    color: #291C12;
    span {
      font-size: 16px;
      line-height: 130%;
      font-weight: 400;
      text-decoration-line: line-through;
      color: #8D8D8D;
      margin-left: 15px;
      display: block;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
  }
  &__btn {
    padding: 0;
    background: transparent;
    width: 26px;
    &--active {
      svg {
        path {
          fill: #E6C79E;;
          stroke: #E6C79E;;
        }
      }
    }
    svg {
      width: 100%;
    }
  }
  &__btn2 {
    width: 45px;
    height: 45px;
    background: #291C12;
    border-radius: 5px;
    margin-left: 15px;
    svg {
      &:nth-child(2) {
        display: none;
      }
    }
    &--active {
      svg {
        &:nth-child(1) {
          display: none;
        }
        &:nth-child(2) {
          display: block;
        }
      }
    }
  }
}
.modal {
  background: rgba(41, 28, 18, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  &__dialog{
    width: 563px;
    max-width: 100%;
    position: relative;
    background: #F6F6F9;
    @media screen and (max-width: 992px) {
      margin: 50px auto 0;
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    position: absolute;
    top: -35px;
    right: 0;
    background: transparent;
    padding: 0;
    margin: 0;
  }
  &__content {
    padding: 65px;
  }
  &__title {
    font-family: 'caesardressingcyrillic';
    font-size: 24px;
    line-height: 120%;
    color: #291C12;
    text-align: center;
  }
  &__form {
    margin-top: 30px;
    &-group {
      margin: 0;
      padding: 0;
      border: none;
      &:not(:first-child) {
        margin-top: 20px;
      }
      input {
        width: 100%;
        height: 60px;
        background: #FFFFFF;
        border-radius: 2px;
        font-weight: 500;
        font-size: 16px;
        line-height: 120%;
        color: #8D8D8D;
        padding: 0 25px;
        border: none;
      }
      button {
        width: 100%;
        height: 60px;
      }
    }
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
}
.fixed-btns {
  position: fixed;
  z-index: 2;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
  background: #291C12;
  box-shadow: 0px 0px 82px rgba(255, 255, 255, 0.17);
  border-radius: 43px 0 0 43px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 30px 20px;
}
.fixed-btn__social {
  &:not(:first-child) {
    margin-top: 30px;
  }
  &:hover {
    svg {
      path {
        fill: #E6C79E;
      }
    }
  }
}
.fixed-btn__up {
  width: 24px;
  height: 43px;
  background: #E6C79E;
  border-radius: 9px;
  margin-top: 37px;
  &:hover {
    background: #ffffff;
  }
}

@media screen and (max-width: 1150px) {
  .recommendations-cards {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (max-width: 992px) {
  .recommendations-card {
    &__hashtags {
      display: none;
    }
    &__title {
      font-size: 12.5px;
    }
    &__weight {
      font-size: 12.5px;
    }
    &__price {
      font-size: 16px;
      span {
        font-size: 12px;
        margin-left: 10px;
      }
    }
    &__btn {
      width: 20px;
    }
    &__btn2 {
      width: 35px;
      height: 35px;
      margin-left: 12px;
    }
  }
}

@media screen and (max-width: 767px) {
  .display2 {
    font-size: 24px;
  }
  .btn-bg {
    font-size: 16px;
  }
  .recommendations-cards {
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }
  .recommendations-card {
    &__title {
      max-width: 130px;
    }
    &__price {
      display: block;
      span {
        margin: 3px 0 0 0;
      }
    }
  }
  .modal {
    &__content {
      padding: 60px 30px;
    }
    &__form {
      &-group {
        input {
          height: 55px;
        }
        button {
          height: 55px;
        }
      }
    }
  }
  .fixed-btns {
    display: none;
  }
}

@media screen and (max-width: 576px) {
  .recommendations-card {
   &__desc {
     position: relative;
   }
    &__btn {
      position: absolute;
      right: 0;
      bottom: 77px;
      width: 37px;
      height: 37px;
      background: #FFFFFF;
      border-radius: 9.32974px 0px 3.10991px;
      svg {
        width: 17px;
        height: 17px;
      }
    }
  }
}

@media screen and (max-width: 380px) {
  .recommendations-cards {
    grid-template-columns: 1fr;
  }
}