.header {
  background: #FFFFFF;
  &-top {
    padding: 15px 0;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__numbers {
      display: flex;
      align-items: center;
    }
    &__btns {
      display: flex;
      align-items: center;
    }
    &__logo {
      font-family: 'caesardressingcyrillic';
      font-size: 28px;
      line-height: 105%;
      color: #291C12;
      text-align: center;
    }
    &__search {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 5px;
        height: 100%;
        background: #E6C79E;
        border-radius: 4px 0 0 4px;
      }
      input {
        width: 260px;
        height: 45px;
        background: #F5F5F5;
        border-radius: 4px;
        font-family: 'Montserrat';
        font-size: 14px;
        line-height: 120%;
        padding: 0 10px 0 30px;
        border: 1px solid #F5F5F5;
        color: #291C12;
        &::placeholder {
          color: #818181;
        }
      }
      button {
        position: absolute;
        top: 0;
        right: 0;
        width: 45px;
        height: 100%;
        background: #E6C79E;
        border-radius: 0px 5px 5px 0px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover {
          background: #291C12;
          svg path {
            stroke: #FFFFFF;
          }
        }
      }
    }
    &__number {
      &:not(:first-child) {
        padding-left: 20px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 1px;
          height: 100%;
          background: #D9D9D9;
        }
      }
      &:not(:last-child) {
        padding-right: 20px;
      }
      span {
        &:nth-child(1) {
          font-weight: 600;
          font-size: 16px;
          line-height: 20px;
          color: #291C12;
          display: block;
        }
        &:nth-child(2) {
          font-size: 14px;
          line-height: 120%;
          color: #8C8C8C;
          display: block;
          margin-top: 5px;
        }
      }
    }
    &__btns {
      display: flex;
      align-items: center;
      background: #E6C79E;
      border-radius: 42px;
      padding: 15px 25px;
    }
    &__search-mob {
      background: transparent;
      display: none;
      padding: 0;
    }
    &__favorites {
      margin-left: 9px;
    }
    &__basket {
      position: relative;
      z-index: 6;
      margin-left: 9px;
      &--active {
        .header-top__basket-hidden {
          opacity: 1;
          pointer-events: all;
        }
      }
      &-hidden {
        position: absolute;
        z-index: 2;
        top: 100%;
        right: -55px;
        padding-top: 20px;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
      &-wrapp {
        width: 336px;
        background: #FFFFFF;
        box-shadow: 0px 0px 83px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        padding: 20px 15px;
        position: relative;
      }
      &-close {
        position: absolute;
        top: -30px;
        right: 0;
        background: transparent;
        padding: 0;
        margin: 0;
        display: none;
      }
      &-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 20px;
        border-bottom: 1px solid #F2F2F2;
      }
      &-title {
        font-weight: 500;
        font-size: 16px;
        line-height: 120%;
        color: #8D8D8D;
      }
      &-price {
        font-weight: 600;
        font-size: 16px;
        line-height: 120%;
        color: #291C12;
      }
      &-items {
        padding-top: 15px;
      }
      &-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 0;
        &:not(:first-child) {
          border-top: 1px solid #F2F2F2;
        }
      }
      &-img {
        width: 67px;
        flex-shrink: 0;
        img {
          width: 100%;
        }
      }
      &-desc {
        margin-left: 8px;
      }
      &-subtitle {
        font-size: 14px;
        line-height: 120%;
        color: #291C12;
        span {
          font-size: 14px;
          line-height: 120%;
          color: #8D8D8D;
          margin-left: 5px;
        }
      }
      &-btn {
        background: transparent;
        padding: 0;
        margin-left: 10px;
      }
      &-calc {
        display: flex;
        align-items: center;
        background: #F4F4F4;
        border-radius: 1.12131px;
        margin: 0 25px;
        height: 27px;
        input {
          width: 20px;
          font-weight: 500;
          font-size: 10.8px;
          line-height: 120%;
          color: #291C12;
          border: unset;
          background: transparent;
          padding: 0;
          margin: 0 2px;
          text-align: center;
        }
      }
      &-minus {
        color: #8D8D8D;
        background: transparent;
      }
      &-plus {
        color: #8D8D8D;
        background: transparent;
      }
      &-link {
        font-weight: 600;
        font-size: 14px;
        line-height: 120%;
        color: #8D8D8D;
        margin-top: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
    &__desc-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__desc-bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-left: 4px;
      margin-top: 10px;
    }
    &__price-one {
      font-weight: 500;
      font-size: 14px;
      line-height: 120%;
      color: #8D8D8D;
    }
    &__price-quantity {
      font-weight: 500;
      font-size: 14px;
      line-height: 120%;
      color: #291C12;
    }
    &__user {
      position: relative;
      z-index: 3;
      margin-left: 9px;
      &-hidden {
        position: absolute;
        z-index: 2;
        top: 100%;
        right: -25px;
        padding-top: 20px;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
        &--active {
          opacity: 1;
          pointer-events: all;
        }
      }
      &-wrapp {
        width: 250px;
        background: #FFFFFF;
        box-shadow: 0px 0px 83px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        padding: 25px 25px 15px 25px;
      }
      &-title {
        font-size: 16px;
        line-height: 120%;
        color: #291C12;
        padding-bottom: 25px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          left: 50%;
          bottom: 0;
          transform: translate(-50%, 0);
          width: calc(100% + 50px);
          height: 1px;
          background: #EEEEEE;
        }
      }
      &-link {
        font-weight: 600;
        font-size: 12px;
        line-height: 120%;
        color: #8D8D8D;
        padding-top: 15px;
        display: flex;
        align-items: center;
        i {
          margin-left: 5px;
        }
      }
    }
  }
  &-bottom {
    padding-top: 15px;
    &__inner {
      display: flex;
     align-items: center;
    }
    &__left {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-shrink: 0;
    }
    &__numbers {
      align-items: center;
      display: none;
    }
    &__number {
      &:not(:first-child) {
        padding-left: 20px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 1px;
          height: 100%;
          background: #D9D9D9;
        }
      }
      &:not(:last-child) {
        padding-right: 20px;
      }
      span {
        &:nth-child(1) {
          font-weight: 600;
          font-size: 16px;
          line-height: 20px;
          color: #291C12;
          display: block;
        }
        &:nth-child(2) {
          font-size: 14px;
          line-height: 120%;
          color: #8C8C8C;
          display: block;
          margin-top: 5px;
        }
      }
    }
    &__catalog {
      position: relative;
      z-index: 0;
      &--active {
        z-index: 5;
      }
      &-show {
        background-image: url("../img/catalog-bg.webp");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        padding: 20px 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        border-radius: 5px 5px 0 0;
        div {
          font-family: 'caesardressingcyrillic';
          font-size: 20px;
          line-height: 120%;
          color: #E6C79E;
          margin-left: 20px;
        }
      }
      &-hidden {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        background: #FFFFFF;
        box-shadow: 0px 0px 83px rgba(0, 0, 0, 0.25);
        border-radius: 0px 0px 5px 5px;
        opacity: 0;
        pointer-events: none;
        transition: all 0.5s;
        &--active {
          opacity: 1;
          pointer-events: all;
        }
      }
      &-wrapp {
        position: relative;
        z-index: 4;
      }
      &-item {
        position: relative;
        @media screen and (min-width: 768px) {
         &:hover {
           .header-bottom__item-hidden {
             opacity: 1;
             pointer-events: all;
           }
         }
        }
        &:hover {
          .header-bottom__catalog-title {
            span {
              font-weight: 500;
            }
          }
          .header-bottom__catalog-btn {
            svg path {
              stroke: #291C12;
            }
          }
        }
        &:not(:first-child) {
          &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate(-50%, 0);
            width: 100%;
            height: 1px;
            background: #EBEBEB;
          }
        }
      }
      &-item2 {
        position: relative;
        @media screen and (min-width: 768px) {
          &:hover {
            .header-bottom__item-hidden2 {
              opacity: 1;
              pointer-events: all;
            }
          }
        }
        &:hover {
          .header-bottom__item-show2 {
            i {
              svg path {
                stroke: #291C12;
              }
            }
          }
          .header-bottom__catalog-subtitle {
            font-weight: 500;
          }
        }
        &:not(:first-child) {
          &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate(-50%, 0);
            width: 100%;
            height: 1px;
            background: #EBEBEB;
          }
        }
      }
      &-title {
        display: flex;
        align-items: center;
        span {
          font-size: 18px;
          line-height: 120%;
          color: #291C12;
          margin-left: 10px;
        }
      }
      &-subtitle {
        font-size: 18px;
        line-height: 120%;
        color: #291C12;
      }
    }
    &__search {
      position: relative;
      padding: 0 20px;
      display: none;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 20px;
        width: 4px;
        height: 100%;
        background: #E6C79E;
        border-radius: 4px 0 0 4px;
      }
      input {
        width: 100%;
        height: 35px;
        background: #F5F5F5;
        border-radius: 4px;
        font-size: 12px;
        line-height: 120%;
        padding: 0 10px 0 30px;
        border: 1px solid #F5F5F5;
        color: #291C12;
        &::placeholder {
          color: #818181;
        }
      }
      button {
        position: absolute;
        top: 0;
        right: 20px;
        width: 45px;
        height: 100%;
        background: #E6C79E;
        border-radius: 0px 5px 5px 0px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover {
          background: #291C12;
          svg path {
            stroke: #FFFFFF;
          }
        }
      }
    }
    &__item {
      &-show {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 25px;
        position: relative;
        cursor: pointer;
      }
      &-show2 {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 22px 20px;
        position: relative;
        cursor: pointer;
      }
      &-hidden {
        position: absolute;
        top: 0;
        left: 100%;
        min-width: 250px;
        background: #FFFFFF;
        box-shadow: 0px 0px 83px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        opacity: 0;
        pointer-events: none;
        transition: all 0.5s;
      }
      &-hidden2 {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 100%;
        width: 230px;
        background: #FFFFFF;
        box-shadow: 0px 0px 83px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        padding: 20px 15px 30px 30px;
        opacity: 0;
        pointer-events: none;
        transition: all 0.5s;
      }
      &-link {
        font-size: 16px;
        line-height: 120%;
        color: #291C12;
        position: relative;
        padding-left: 15px;
        display: block;
        &:hover {
          font-weight: 500;
          color: #291C12;
          &:after {
            background: #291C12;
          }
        }
        &:not(:first-child) {
          margin-top: 20px;
        }
        &:after {
          content: '';
          position: absolute;
          top: 5px;
          left: 0;
          width: 8px;
          height: 8px;
          background: #E6C79E;
          border-radius: 50%;
        }
      }
      &-close {
        align-items: center;
        background: #f7f8fa;
        padding: 15px 20px;
        cursor: pointer;
        display: none;
        i {
          width: 7px;
          svg {
            width: 100%;
          }
        }
        span {
          font-size: 14px;
          line-height: 120%;
          color: #999;
          margin-left: 15px;
        }
      }
      &-close2 {
        align-items: center;
        background: #f7f8fa;
        padding: 15px 20px;
        cursor: pointer;
        display: none;
        width: calc(100% + 45px);
        margin-left: -30px;
        margin-top: -20px;
        i {
          width: 7px;
          svg {
            width: 100%;
          }
        }
        span {
          font-size: 14px;
          line-height: 120%;
          color: #999;
          margin-left: 15px;
        }
      }
    }
    &__list {
      display: flex;
      align-items: center;
      li {
        font-size: 16px;
        line-height: 120%;
        color: #291C12;
        margin: 0 75px;
        position: relative;
        &:not(:last-child) {
          &:after {
            content: '';
            position: absolute;
            top: 50%;
            right: -74px;
            transform: translate(0, -50%);
            width: 1px;
            height: 32px;
            background: #D9D9D9;
          }
        }
        &:hover {
          font-weight: 600;
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &-bottom {
      &__list {
        li {
          margin: 0 25px;
          &:not(:last-child) {
            &:after {
              right: -24px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &-top {
      &__logo {
        font-size: 24px;
      }
      &__search {
        display: none;
        &-mob {
          display: block;
        }
      }
    }
    &-bottom {
      &__catalog {
        &-show {
          padding: 15px 20px;
          i {
            svg {
              width: 28px;
              height: 20px;
            }

          }
          div {
            font-size: 18px;
            margin-left: 15px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .header {
    padding-bottom: 20px;
    &-top {
      &__logo {
        font-size: 20px;
        br {
          display: none;
        }
      }
      &__numbers {
        display: none;
      }
      &__btns {
        padding: 12px 20px;
      }
      &__search-mob {
        svg {
          width: 18px;
          height: auto;
        }
      }
      &__favorites {
        svg {
          width: 19px;
          height: auto;
        }
      }
      &__basket {
        &--active {
          .header-top__basket-hidden {
            position: fixed;
            z-index: 4;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            padding: 0;
            background: rgba(41, 28, 18, 0.7);
          }
        }
      }
      &__basket-show {
        svg {
          width: 19px;
          height: auto;
        }
      }
      &__basket-wrapp {
        margin: 50% auto 0;
        transform: translate(0, -50%);
      }
      &__basket-close {
        display: block;
      }
      &__user-show {
        svg {
          width: 15px;
          height: auto;
        }
      }
    }
    &-bottom {
      padding-top: 0;
      padding-bottom: 20px;
      border-bottom: 1px solid #F2F2F2;
      &__inner {
        display: block;
      }
      &__right {
        margin-top: 20px;
      }
      &__catalog {
        margin-left: -15px;
        width: 110px;
        height: 46px;
        &-show {
          justify-content: start;
          border-radius: 0 5px 5px 0;
          &--active {
            position: fixed;
            top: 0;
            left: 0;
            width: 290px;
            background: #ffffff;
            background-image: url("../img/close.svg");
            background-repeat: no-repeat;
            background-position: top 50% right 20px;
            background-size: 25px;
            z-index: 4;
            i {
              svg path {
                stroke: #291C12;
              }
            }
            div {
              color: #291C12;
            }
          }
          i {
            svg {
              width: 19px;
              height: auto;
            }
          }
          div {
            font-size: 12px;
            margin-left: 10px;
            span {
              display: none;
            }
          }
        }
        &-hidden {
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          height: 100vh;
          background: rgba(41, 28, 18, 0.7);
          box-shadow: unset;
        }
        &-wrapp {
          padding-top: 47px;
          width: 290px;
          height: 100%;
          background: #ffffff;
        }
        &-title {
          i {
            width: 18px;
            img {
              width: 100%;
              height: auto;
            }
          }
          span {
            font-size: 14px;
            margin-left: 8px;
          }
        }
        &-btn {
          svg {
            width: 5px;
          }
        }
        &-subtitle {
          font-size: 14px;
        }
      }
      &__search {
        display: block;
      }
      &__item {
        &-show {
          padding: 15px 20px;
          &--active {
            .header-bottom__catalog-title {
              span {
                font-weight: 500;
              }
            }
            .header-bottom__catalog-btn {
              svg path {
                stroke: #291C12;
              }
            }
          }
        }
        &-show2 {
          padding: 15px 20px;
          &--active {
            .header-bottom__catalog-subtitle {
              span {
                font-weight: 500;
              }
            }
            i {
              svg path {
                stroke: #291C12;
              }
            }
          }
          i {
            svg {
              width: 5px;
            }
          }
        }
        &-hidden {
          position: fixed;
          z-index: 3;
          top: 0;
          left: -100%;
          min-width: 290px;
          height: 100vh;
          opacity: 1;
          pointer-events: all;
          box-shadow: unset;
          &--active {
            left: 0;
          }
        }
        &-hidden2 {
          position: fixed;
          z-index: 3;
          top: 0;
          left: -100%;
          min-width: 290px;
          height: 100vh;
          opacity: 1;
          pointer-events: all;
          box-shadow: unset;
          &--active {
            left: 0;
          }
        }
        &-close {
          display: flex;
        }
        &-close2 {
          display: flex;
        }
      }
      &__numbers {
        display: flex;
        margin-left: 5px;
      }
      &__number {
        &:not(:first-child) {
          padding-left: 8px;
        }
        &:not(:last-child) {
          padding-right: 8px;
        }
        span {
          &:nth-child(1) {
            font-size: 12px;
          }
          &:nth-child(2) {
            font-size: 11px;
          }
        }
      }
      &__list {
        li {
          font-size: 14px;
          margin: 0;
          &:not(:last-child) {
            margin-right: 22px;
            &:after {
              right: -11px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &-top {
      &__basket-wrapp {
        margin: 75% auto 0;
      }
    }
    &-bottom {
      &__list {
        justify-content: center;
      }
    }
  }
}

@media screen and (max-width: 385px) {
  .header {
    &-bottom {
      &__catalog {
        width: 90px;
        height: 37px;
        &-show {
          padding: 10px 7px;
        }
      }
      &__list {
        li {
          font-size: 12px;
          &:after {
            display: none;
          }
          &:not(:last-child) {
            margin-right: 12px;
          }
        }
      }
    }
  }
}