.main {
  padding: 70px 0;
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  overflow: hidden;
  margin-top: -8px;
  position: relative;
  &__inner {
    text-align: center;
    position: relative;
    padding: 135px 0;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.83);
      filter: blur(132.5px);
    }
  }
  &__wrapp {
    position: relative;
    z-index: 2;
  }
  &__title {
    font-family: 'caesardressingcyrillic';
    font-size: 61px;
    line-height: 120%;
    color: #291C12;
    br {
      display: none;
    }
  }
  &__subtitle {
    font-weight: 500;
    font-size: 24px;
    line-height: 120%;
    color: #291C12;
    margin-top: 30px;
  }
  &__btn {
    width: 160px;
    height: 50px;
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    color: #FFFFFF;
    background: #291C12;
    border-radius: 3px;
    margin: 30px auto 0;
  }
}
.advantages {
  padding: 0 0 70px 0;
  overflow: hidden;
  position: relative;
  z-index: 1;
  &__top {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: calc(100% + 10000px);
      height: 1px;
      background: #F5F5F5;
    }
    &-item {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      padding: 70px 0;
      &:not(:last-child) {
        border-right: 1px solid #F5F5F5;
      }
    }
    &-icon {
      width: 62px;
      height: 62px;
      background: #E6C79E;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      img {
        max-width: 100%;
      }
    }
    &-title {
      font-family: 'caesardressingcyrillic';
      font-size: 24px;
      line-height: 120%;
      font-weight: 400;
      color: #291C12;
      margin-left: 15px;
    }
  }
  &__info {
    margin-top: 100px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    text-align: center;
    &-item {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: column;
      &1 {
        background: #F8F8F8;
        border-radius: 5px;
      }
      &2 {
        display: grid;
        grid-template-columns: 1fr;
        gap: 20px;
      }
      &3 {
        background: #F8F8F8;
        border-radius: 5px;
      }
    }
    &-triangle {
      position: absolute;
      z-index: 3;
      top: 25px;
      right: -30px;
      pointer-events: none;
      &2 {
        position: absolute;
        z-index: 2;
        top: 25px;
        right: -50px;
        pointer-events: none;
      }
      &3 {
        position: absolute;
        z-index: 2;
        left: 50%;
        bottom: -45px;
        transform: translate(-50%, 0) rotate(90deg);
        pointer-events: none;
      }
      &4 {
        position: absolute;
        z-index: 1;
        top: -45px;
        left: 50%;
        transform: translate(-50%, 0) rotate(90deg);
        pointer-events: none;
      }
      &5 {
        position: absolute;
        z-index: 2;
        top: 50%;
        right: -30px;
        transform: translate(0, -50%);
        pointer-events: none;
      }
      &6 {
        position: absolute;
        z-index: 1;
        top: 50%;
        right: -50px;
        transform: translate(0, -50%);
        pointer-events: none;
      }
      &7 {
        position: absolute;
        z-index: 2;
        left: 50%;
        bottom: -45px;
        transform: translate(-50%, 0) rotate(90deg);
        pointer-events: none;
        display: none;
      }
      &8 {
        position: absolute;
        z-index: 1;
        left: 50%;
        bottom: -65px;
        transform: translate(-50%, 0) rotate(90deg);
        pointer-events: none;
        display: none;
      }
    }
    &-top {
      padding: 45px 27px 15px;
      &2 {
        position: relative;
       padding: 60px 75px;
        background: #F8F8F8;
        border-radius: 5px;
      }
    }
    &-bottom {
      width: 100%;
      padding: 60px 10px 35px;
      background-image: url("../img/advantages-info.webp");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      &2 {
        position: relative;
        background: #F8F8F8;
        border-radius: 5px;
        padding: 50px 50px 36px;
      }
      &3 {
        background-image: url("../img/advantages-info2.webp");
      }
    }
    &-icon {
      position: absolute;
      top: -20.5px;
      left: 50%;
      transform: translate(-50%, 0);
      &:after {
        content: '';
        position: absolute;
        z-index: 1;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 62px;
        height: 62px;
        background: #FFFFFF;
        border-radius: 50%;
      }
      i {
        width: 41px;
        height: 41px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #E6C79E;
        border-radius: 50%;
        margin: 0 auto;
        position: relative;
        z-index: 2;
        img {
          width: 19px;
          height: auto;
        }
      }
    }
    &-text {
      font-size: 14px;
      line-height: 120%;
      color: #3D3D3D;
    }
    &-desc {
      display: flex;
      align-items: center;
      &2 {
        flex-direction: column;
      }
    }
    &-hidden {
      background: #FFFFFF;
      box-shadow: 0px 0px 83px rgba(0, 0, 0, 0.25);
      border-radius: 5px;
      padding: 27px 15px;
      max-width: 235px;
      font-size: 12px;
      line-height: 120%;
      color: #3D3D3D;
      position: relative;
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
      &--active {
        opacity: 1;
        pointer-events: all;
      }
      &:after {
        content: '';
        position: absolute;
        top: 5px;
        left: 5px;
        width: calc(100% - 10px);
        height: calc(100% - 10px);
        border: 1px solid #FFEBD0;
        border-radius: 5px;

      }
    }
    &-show {
      width: 31px;
      height: 31px;
      background: #291C12;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      cursor: pointer;
      margin-left: 10px;
      &2 {
        margin: 0 0 5px 0;
      }
    }
    &-subtitle {
      font-weight: 500;
      font-size: 14px;
      line-height: 120%;
      color: #000000;
      background: #E6C79E;
      border-radius: 3px;
      padding: 5px 10px;
      margin-top: 8px;
    }
    &-logo {
      width: 153px;
      margin: 38px auto 0;
      img {
        width: 100%;
      }
    }
  }
}
.advantages-cards {
  padding: 70px 0;
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
  }
  &__card {
    position: relative;
    min-height: 440px;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    &:hover {
      .advantages-cards__card-images {
        height: 487px;
      }
      .advantages-cards__desc-wrapp {
        i {
          width: 45px;
        }
      }
    }
    &-images {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 440px;
      transition: all 0.6s;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-img2 {
      display: none;
    }
  }
  &__desc-wrapp {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(6.578px);
    padding: 22px 30px;
    span {
      font-family: 'caesardressingcyrillic';
      font-size: 30px;
      line-height: 120%;
      color: #FFFFFF;
    }
    i {
      position: relative;
      width: 22px;
      height: 22px;
      overflow: hidden;
      transition: all 0.3s;
      svg {
        position: absolute;
        top: 0;
        right: 0;
      }
    }
  }
}
.delivery {
  padding: 70px 0;
  &__inner {
    display: flex;
    align-items: center;
  }
  &__left {
    max-width: calc(100% - 600px);
  }
  &__right {
    max-width: 560px;
    margin-left: 40px;
  }
  &__content {
    &--active {
      display: none;
    }
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__icons {
    display: flex;
    justify-content: center;
    align-items: center;
    i {
      width: 68px;
      img {
        width: 100%;
      }
      &:not(:first-child) {
        margin-left: 20px;
      }
    }
  }
  &__title {
    margin-top: 30px;
  }
  &__info {
    font-size: 16px;
    line-height: 120%;
    color: #291C12;
    margin-top: 40px;
  }
  &__content-hide {
    text-align: center;
    display: none;
    &--active {
      display: block;
    }
  }
  &__form {
    display: flex;
    align-items: center;
    position: relative;
    margin-top: 40px;
   &-input {
     position: relative;
     &:after {
       content: '';
       position: absolute;
       top: 0;
       left: 0;
       width: 5px;
       height: 100%;
       background: #E6C79E;
       border-radius: 4px 0 0 4px;
     }
     input {
       width: 315px;
       height: 63px;
       background: #F3F3F3;
       border-radius: 5px 0 0 5px;
       font-size: 16px;
       line-height: 120%;
       padding: 0 10px 0 30px;
       border: 1px solid #F5F5F5;
       color: #291C12;
     }
   }
    &-btn {
      max-width: 176px;
      width: 100%;
      height: 63px;
      font-family: 'caesardressingcyrillic';
      font-size: 20px;
      line-height: 120%;
      color: #FFFFFF;
      border-radius: 0 5px 5px 0;
      background-image: url("../img/catalog-bg.webp");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      &:hover {
        background-image: url("../img/delivery-btn.webp");
        color: #291C12;
      }
    }
  }
  &__hide {
    &-icon {
      width: 95px;
      margin: 35px auto 0;
      img {
        width: 100%;
      }
    }
    &-title {
      margin-top: 35px;
    }
    &-btn {
      width: 250px;
      height: 63px;
      margin: 35px auto 0;
    }
  }
}
.recommendations {
  padding: 70px 0;
  &__top {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    i {
      width: 68px;
      img {
        width: 100%;
      }
    }
  }
  &__title {
    margin: 0 25px;
  }
}
.about {
  padding: 70px 0 150px;
  &__top {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    i {
      width: 68px;
      img {
        width: 100%;
      }
    }
  }
  &__title {
    margin: 0 25px;
  }
  &__wrapp {
    display: flex;
    align-items: center;
    margin-top: 50px;
  }
  &__left {
    max-width: 535px;
    margin-right: 37px;
    position: relative;
  }
  &__right {
    max-width: calc(100% - 572px);
  }
  &__desc {
    p {
      font-size: 16px;
      line-height: 120%;
      color: #3D3D3D;
      &:not(:first-child) {
        margin-top: 35px;
      }
    }
  }
  .swiper-slide {
    width: 400px;
    img {
      width: 100%;
    }
  }
  .swiper-button-prev {
    top: 95%;
    left: -5px;
    width: 73px;
    height: 33px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #291C12;
    border-radius: 5px;
    &:after {
      display: none;
    }
  }
  .swiper-button-next {
    top: 95%;
    right: -5px;
    width: 73px;
    height: 33px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #291C12;
    border-radius: 5px;
    &:after {
      display: none;
    }
  }
  .swiper-slide-shadow-left,
  .swiper-slide-shadow-right {
    background: rgba(41, 28, 18, 0.6);;
  }
}

