    &__title {
      br {
        display: block;
      }
    }
  }
  .advantages {
    padding: 100px 0 75px;
    &__top {
      grid-template-columns: 1fr;
      gap: 50px;
      &:after {
        display: none;
      }
      &-item {
        padding: 0;
        &:not(:last-child) {
          border: none;
        }
      }
    }
  }
  .advantages-cards {
    &__card {
      min-height: 132px;
    }
  }
  .delivery {
    &__info {
      max-width: 318px;
      margin: 30px auto 0;
      br {
        display: none;
      }
    }
    &__form-input {
      input {
        width: 215px;
      }
    }
    &__form-btn {
      max-width: 138px;
    }
  }
  .recommendations {
    &__title {
      font-size: 22px;
      margin:  0 15px;
    }
  }
}

@media screen and (max-width: 380px) {
  .about {
    &__left {
      max-width: 250px;
    }
    .swiper-slide {
      width: 200px;
    }
    .swiper-button-prev {
      left: -22px;
    }
    .swiper-button-next {
      right: -22px;
    }
  }
  .delivery {
    &__form-input {
      input {
        width: 180px;
      }
    }
  }
}