.footer {
  background: #291C12;
  padding: 20px 0;
  position: relative;
  z-index: 1;
  &:after {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("../img/footer-bg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  &-top {
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__logo {
      width: 116px;
      flex-shrink: 0;
      display: block;
      img {
        width: 100%;
      }
    }
    &__list {
      display: flex;
      align-items: center;
      margin-left: 50px;
      li {
        font-size: 16px;
        line-height: 120%;
        color: #FFFFFF;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          right: -75px;
          transform: translate(0, -50%);
          width: 1px;
          height: 32px;
          background: rgba(141, 141, 141, 0.1);;
        }
        &:not(:first-child) {
          margin-left: 150px;
        }
        &:hover {
          font-weight: 600;
        }
      }
    }
  }
  &-center {
    margin-top: 10px;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__location {
      display: flex;
      align-items: center;
      max-width: 225px;
      span {
        font-size: 14px;
        line-height: 120%;
        color: #BBBBBB;
        margin-left: 10px;
      }
    }
    &__nav {
      display: flex;
      align-items: center;
      margin-left: 15px;
    }
    &__icon {
      width: 68px;
      img {
        width: 100%;
      }
    }
    &__list {
      display: flex;
      align-items: center;
      margin: 0 20px;
      li {
        font-family: 'caesardressingcyrillic';
        font-size: 24px;
        line-height: 120%;
        color: #E6C79E;
        &:not(:first-child) {
          margin-left: 75px;
        }
        &:hover {
          color: #FFFFFF;
        }
      }
    }
    &__btns {
      flex-shrink: 0;
      margin-left: 15px;
    }
    &__tel {
      font-weight: 600;
      font-size: 24px;
      line-height: 120%;
      color: #FFFFFF;
      display: block;
      text-align: center;
    }
    &__btn {
      width: 211px;
      height: 44px;
      background: #E6C79E;
      border-radius: 5px;
      font-weight: 600;
      font-family: 'Montserrat';
      font-size: 16px;
      line-height: 120%;
      text-transform: uppercase;
      color: #291C12;
      position: relative;
      margin-top: 5px;
      &-icon {
        position: absolute;
        z-index: 2;
        left: 50%;
        bottom: -8px;
        transform: translate(-50%, 0);
      }
      &-icon2 {
        position: absolute;
        left: 50%;
        bottom: -20px;
        transform: translate(-50%, 0);
        pointer-events: none;
      }
    }
  }
  &-bottom {
    margin-top: 60px;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__politic {
      font-size: 14px;
      line-height: 120%;
      color: #BBBBBB;
    }
    &__socials {
      display: flex;
      align-items: center;
    }
    &__social {
      &:not(:first-child) {
        margin-left: 13px;
      }
      &:hover {
        svg {
          path {
            fill: #ffffff;
          }
        }
      }
    }
    &__info {
      font-size: 14px;
      line-height: 120%;
      color: #BBBBBB;
    }
  }
  &-mob {
    display: none;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 40px;
    }
    &__center {
      margin: 0 25px;
    }
    &__socials {
      margin-top: 15px;
    }
    &__btns {
      margin-left: 0;
    }
    &__info {
      margin-top: 15px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    &-top {
      &__list {
        li {
          &:not(:first-child) {
            margin-left: 100px;
          }
          &:after {
            right: -50px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1150px) {
  .footer {
    &-center {
      margin-top: 40px;
      &__inner {
        justify-content: center;
      }
      &__location {
        display: none;
      }
      &__btns {
        display: none;
      }
    }
    &-bottom {
      display: none;
    }
    &-mob {
      display: block;
      &__location {
        display: block;
      }
      &__btns {
        display: block;
      }
    }
  }
}

@media screen and (max-width: 1050px) {
  .footer {
    &-top {
      &__list {
        margin-left: 0px;
        li {
          &:not(:first-child) {
            margin-left: 50px;
          }
          &:after {
            right: -25px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &-mob {
      &__tel {
        font-size: 20px;
      }
      &__btn {
        width: 175px;
        height: 35px;
        font-size: 13px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &-top {
      &__inner {
        display: block;
      }
      &__logo {
        width: 84px;
        margin: 0 auto;
      }
      &__list {
        flex-direction: column;
        margin-top: 25px;
        li {
          &:after {
            display: none;
          }
          &:not(:first-child) {
            margin: 20px 0 0 0;
          }
        }
      }
    }
    &-center {
      display: none;
    }
    &-mob {
      margin-top: 25px;
      &__inner {
        flex-direction: column;
        text-align: center;
      }
      &__left {
        margin-top: 35px;
      }
      &__center {
        order: -2;
        margin: 0;
      }
      &__right {
        margin-top: 20px;
      }
      &__socials {
        justify-content: center;
      }
      &__info {
        margin-top: 5px;
      }
    }
  }
}